*,
::after,
::before {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
a {
  text-decoration: none;
  color: black;
}
img {
  max-width: 100%;
  height: auto;
}
@font-face {
  font-family: "Oswald";
  src: local("Oswald medium"), url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: local("Oswald regular"), url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
.fieldset {
  border: none;
  padding: 0;
  margin: 0;
}
.button {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  height: 40px;
  background-color: #68b738;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.button--add {
  margin-top: 8px;
}
@media (min-width: 768px) {
  .button--add {
    grid-column-start: 1;
    grid-column-end: -1;
  }
}
@media (min-width: 1280px) {
  .button--add {
    margin: 0;
    width: 244px;
    grid-column-start: 3;
    align-self: center;
  }
}
.button:hover,
.button:focus-visible {
  background-color: #5eaa2f;
  transition: all 0.3s ease-in-out;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.page {
  height: 100%;
}
.page__body {
  position: relative;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: black;
  background-color: #ffffff;
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
}
@media (min-width: 768px) {
