.optionally {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 24px;
  padding-left: 28px;
  padding-top: 1px;

  @media (min-width: @tablet-width) {
    grid-template-columns: 1fr 1fr;
    row-gap: 44px;
    column-gap: 7px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 78px;
    padding-left: 0;
    width: 1220px;
    margin: 0 auto;
  }
}

.optionally__input {
  &:hover + label::before,
  &:focus-visible + label::before {
    border-color: #bababa;
  }

  // &:hover + label,
  // &:focus-visible + label {
  //   color: #bababa;
  // }

  &:disabled + label {
    opacity: 0.3;
  }

  & + label {
    .text();
    position: relative;
    padding-left: 17px;
    cursor: pointer;

    @media (min-width: @tablet-width) {
      .resize-text(20px, 30px);
      padding-left: 71px;
    }

    @media (min-width: @desktop-width) {
      padding-left: 59px;
    }

    &::before {
      position: absolute;
      content: "";
      width: 30px;
      height: 30px;
      border: 1px solid #cdcdcd;
      left: -28px;
      top: -5px;

      @media (min-width: @tablet-width) {
        width: 40px;
        height: 40px;
        left: 12px;
      }

      @media (min-width: @desktop-width) {
        left: 1px;
      }
    }
  }

  &:checked + label::after {
    content: url("../img/icon/icon-check.svg");
    position: absolute;
    left: -22px;
    top: 1px;

    @media (min-width: @tablet-width) {
      left: 23px;
    }

    @media (min-width: @desktop-width) {
      left: 11px;
    }
  }
}
